.container {
  overflow-x: auto;
  margin-right: auto;
  margin-left: auto;
  margin-top: 50px;
  padding: 0px;
  margin: 10px;
  height: 61vh;
  position: relative;
}

tr {
  height: 40px !important;
  word-wrap: break -word;
}

th {
  max-width: 125px;
  min-width: 125px;
  padding: 0 !important;
  overflow-x: auto;
  white-space: nowrap;
  text-align: center !important;
}

td {
  height: 40px !important;
  padding: 5px !important;
  text-align: center !important;
}

.nodata{
  left: 45%;
  font-size: 30px;
  position: absolute;
  text-align: center;
}

.loading{
  width: 100px;
	height: 100px;
  
  position: absolute;
  top: 50%;
  left: 50%;
  
  transform: translate(-50%, -50%);
}
